//
// --------------------------------------------------
// 模块：前台页面
// --------------------------------------------------
// bootstrap 默认Less
@import "../bootstrap/variables.less";
@import "../bootstrap/utilities.less";
@import "../bootstrap/mixins/clearfix.less";
@import "../bootstrap/mixins/center-block.less";
@import "../bootstrap/mixins/hide-text.less";
// 思诺博UI Less
@import "../sinoui-variables.less";    // 变量

.item-box{
    padding-right: 10px;
    .item-list{
        margin-bottom: 10px;
        border: @navbar-menu-border 1px solid;
        h1{
            font-size:12px;
            text-align: left;
            font-weight: normal;
            padding:6px 5px;
            margin: 0;
            background-color: @sino-form-bg-color;
            border-bottom: @navbar-menu-border 1px solid;
            height: 40px;
            color: #000;
        }
        .sub-tit{
            border-bottom: @navbar-menu-border 1px solid;
            font-weight: 400;
            padding:2px 6px;
            span{
                padding:5px;
                display: inline-block;
                color:#0C91DD;
            }
            a{
                padding:5px;
                color: #D7D7D7;
                float: right;
                border-left: solid 1px @navbar-menu-border;
            }
        }
        .caption{

            border-top: none;
            .content{
                padding:4px 5px;
                height:120px;
                border-bottom: @navbar-menu-border 1px solid;
                position: relative;
                .clearfix();
                .response-img{
                    height: 110px;
                    &.response-img-full{
                        height: 110px;
                    }
                    .item-list-right{
                    }
                }
                .operation{
                    span{
                        margin: 5px;
                        border: @navbar-menu-border 1px solid;
                        border-radius: 4px;
                        padding: 2px;
                        cursor: pointer;
                    }
                }
                p{
                    margin-bottom: 2px;
                    color: @gray-light;
                    span{
                        color: #000;
                    }
                }
                h3{
                    margin: 0;
                    font-size: 12px;
                    span{
                        font-size: 14px;
                    }
                }
                .btn-addcar{
                    position: absolute;
                    right:10px;
                    bottom: 10px;
                    .addcar{
                        color: @white;
                        background: @orange;
                        padding:1px 5px;
                        border-radius: 3px;
                        text-decoration: none;
                        i{
                            margin-right: 5px;
                            font-size: 14px;
                        }
                    }


                }
                .collection{
                    color: #D7D7D7;
                    cursor: pointer;
                    position: absolute;
                    right:10px;
                    .active{
                        color: @orange;
                    }
                }
            }
            .company-tit{
                padding:2px 6px;
            }
        }

        &:hover{
            border-color: @orange;
        }
    }
}

.item-header{
    margin-right: -15px;
    .checkbox{
        input{
            width: 15px;
            height: 15px;
            display: inline-block;
            margin-top: 1px;
        }
    }
    .btn-clear{
        background:transparent;
    }
    .operation-mode-box{
        margin-top: -6px;
        padding-bottom: 6px;
        height: 40px;
        line-height: 40px;
        color: #777;
        .operation-mode{
            border-left: 1px solid @navbar-menu-border;
            margin-right:15px;
            .dis-mode{
                height: 40px;
                line-height: 40px;
                display: inline-block;
                padding: 0 10px;
                border-right: 1px solid @navbar-menu-border;
                font-size: 16px;
                cursor: pointer;
                color:#777;
                &.active{
                    color: @orange;
                    background: @white;
                }
            }

        }
        .dis-page{
            span{
                height: 24px;
                line-height: 16px;
                display: inline-block;
                cursor: pointer;
                padding: 3px 7px;
                border: 1px solid @navbar-menu-border;
                margin-right: -5px;
                &:first-child{
                    border-right: none;
                }
                &.active{
                    background: @white;
                }
            }
        }

    }
}
.modal-purpose-md{
    padding: 30px 0 50px 0;
    li{
        a{
            padding: 6px 5px;
            display: block;
            text-decoration: none;
            border: 1px solid #eeeeee;
            color: #555555;
            &:hover{
                border-color:@orange;

            }
        }
        .check{
            position: absolute;
            bottom:-2px;
            right:15px;
            color: @white;
            z-index: 10;
            display: none;
        }

        &:hover,
        &:focus {
            .check{
                display: block;
            }
            &:after{
                content: "";
                position: absolute;
                bottom:0;
                right:15px;
                width: 10px;
                height: 10px;
                border-bottom: 20px solid @orange;
                border-left: 20px solid transparent;
            }
        }
    }
}

.modal-purpose-sm{
    padding: 0;
    li{
        float: left;
        margin-right: 4px;
        position: relative;
        background:@white;
        a{
            padding: 4px 10px;
            display: block;
            text-decoration: none;
            border: 1px solid #eeeeee;
            color: #555555;
        }
        .check{
            position: absolute;
            bottom:-2px;
            right:0px;
            color: @white;
            z-index: 10;
            display: none;

        }
        &.active{
            .check{
                display: block;
            }
            a{
                border-color:@orange;
            }
            &:after{
                content: "";
                position: absolute;
                bottom:0;
                right:0px;
                width: 10px;
                height: 10px;
                border-bottom: 20px solid @orange;
                border-left: 20px solid transparent;
            }
        }
    }
}

.despatch-table{
    thead{
        >tr{
            >th{
                text-align: left;
                label{
                    min-height: @line-height-computed; // Ensure the input doesn't jump when there is no text
                    padding-left: 35px;
                    margin-bottom: 0;
                    cursor: pointer;
                    margin-right: 10px;
                    font-size: 14px;
                    font-weight: bold;
                    input[type="checkbox"]{
                        position: absolute;
                        margin-left: -20px;
                        margin-top: 4px \9;
                    }
                }
            }
        }
    }
    tbody{
        >tr{
            >td{
                text-align: left;
                label{
                    min-height: @line-height-computed; // Ensure the input doesn't jump when there is no text
                    padding-left: 20px;
                    margin-bottom: 0;
                    font-weight: normal;
                    cursor: pointer;
                    margin-right: 10px;
                    padding-top: 10px;
                    input[type="checkbox"]{
                        position: absolute;
                        margin-left: -20px;
                        margin-top: 4px \9;
                    }
                }
            }
            &:nth-of-type(odd){
                background-color: @white;
            }
        }
    }
}
.apply-item{
    background:#EAF4FE;
    margin-top: -5px;
    box-shadow: 0 0 6px rgba(117, 117, 117, 0.5);
    -webkit-box-shadow: 0 0 6px rgba(117, 117, 117, 0.5);
    .container{
        padding:5px 0;
        span{
            font-size: 16px;
            font-weight: 800;
            display: inline-block;
            padding-top:3px;
        }

    }
}
.apply-sino-form{
    border-left: none;
    border-right:none;
}
.form-line{
    margin:0 -10px 6px -10px;
    border-bottom:solid 1px @sino-form-border;
}
.nav-tabs-title{
    &.nav-tabs-title-apply{
        border-bottom: none;
        li{
            strong{
                font-size: 14px;
                padding:6px 0 0 0;
                display: block;
            }
        }
    }

}
.fixed-top{
    width: 100%;
    background: #fff;
    z-index: 100;
    position: fixed;
    top:0;
    left: 0;
}

.order-table{
    > thead > tr > th.text-left {
        text-align: left!important;
    }
    >tbody>tr>td{
        vertical-align: middle;
        .fa-map-marker{
            font-size: 14px;
            margin-right: 5px;
        }
    }
}
.table .table.order-table-more{
    background:#ffffff;
}
.order-table-more{
    > thead > tr{
        background:#EFEFEF;
    }
    > tbody > tr > td{
        padding: 5px 8px;
    }
    > thead > tr > th.text-left {
        text-align: left!important;
    }
}
.table-striped.order-table-more>tbody>tr:nth-of-type(odd){
    background:#FAFAFA;
}
.btn-press{
    display: inline-block;
    margin-bottom: 0; // For input.btn
    font-weight: @btn-font-weight;
    text-align: center;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
    white-space: nowrap;
    border: 1px solid transparent;
    border-radius: 4px;
    padding:0 3px;
    margin-left:5px;
    line-height: 15px;
}
.btn-press-warning{
    border:solid 1px #BBBBBB;
    background: #F2F4F3;
}
.collection{
    color: #ccc;
    &.active{
        color: @orange;
    }
}

.breadcrumb-arrow {
    padding: @breadcrumb-padding-vertical @breadcrumb-padding-horizontal;
    margin-bottom: @line-height-computed;
    list-style: none;
    border-radius: @border-radius-base;

    > li {
        display: inline-block;

        + li:before {
            content: "@{breadcrumb-separator}\00a0"; // Unicode space added since inline-block means non-collapsing white-space
            padding: 0 5px;
            color: @breadcrumb-color;
        }
    }

    > .active {
        color: @breadcrumb-active-color;
    }
}
